.card__text {
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  color: #000000;
  margin: 0;
  grid-row: 2;
  grid-column: 1;
  padding-left: 20px;
  padding-top: 25px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
